import React from 'react';
import PropTypes from 'prop-types';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Fade from 'react-bootstrap/Fade';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInstagram, faTwitter, faFacebook } from '@fortawesome/free-brands-svg-icons';
import './navbar.css';

const NavBar = React.forwardRef((props, ref) => {
	const { expandedNav } = props;
	return (
		<Fade timeout={3000} in={expandedNav} unmountOnExit>
			<Navbar bg="primary" fixed="top">
				<Navbar.Brand href="#home">
					<svg
						width="52px"
						height="52px"
						viewBox="0 0 104 94"
						version="1.1"
						xmlnsXlink="http://www.w3.org/1999/xlink"
						xmlns="http://www.w3.org/2000/svg"
					>
						<defs>
							<image
								width="103"
								height="94"
								// eslint-disable-next-line max-len
								xlinkHref="data:image/png;base64,"
								id="brandLogo"
							/>
						</defs>
						<use xlinkHref="#brandLogo" fill="#FFFFFF" stroke="none" />
					</svg>
				</Navbar.Brand>
				<Nav className="w-100" justify>
					<Nav.Item href="#Home">Who we are</Nav.Item>
					<Nav.Item href="#About">E-Commerce</Nav.Item>
					<Nav.Item href="#Contact">Contact Us</Nav.Item>
					<div
						style={{
							display: 'flex',
							'flexDirection': 'row',
							'justifyContent': 'space-between',
							'flexBasis': 0,
							'flexGrow': 1,
						}}
					>
						<Nav.Item href="#">
							<FontAwesomeIcon icon={faInstagram} />
						</Nav.Item>
						<Nav.Item href="#">
							<FontAwesomeIcon icon={faTwitter} />
						</Nav.Item>
						<Nav.Item href="#">
							<FontAwesomeIcon icon={faFacebook} />
						</Nav.Item>
					</div>
				</Nav>
			</Navbar>
		</Fade>
	);
});
NavBar.propTypes = {
	expandedNav: PropTypes.bool.isRequired,
};

export default NavBar;
